@font-face {
  font-family: "opensans";
  font-weight: 400;
  font-style: normal;
  src: url("../../fonts/opensans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "opensans";
  font-weight: 700;
  font-style: normal;
  src: url("../../fonts/opensansbold.woff2") format("woff2");
  font-display: swap;
}
.page {
  height: 100%;
}
.page__body {
  min-height: 100%;
  min-width: 320px;
  box-sizing: border-box;
  font-family: "opensans", sans-serif;
  margin: 0;
}
@media (min-width: 768px) {
  .page__body {
    min-width: 768px;
  }
}
@media (min-width: 1150px) {
  .page__body {
    min-width: 1150px;
  }
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
.input {
  width: 260px;
  border: none;
  font-size: 17px;
  line-height: 18px;
  color: rgba(0, 0, 0, 0.3);
  border-bottom: 2px solid #000000;
}
.main-header {
  width: 100vw;
  margin: 0 auto;
  position: relative;
  background-color: #ffffff;
  color: #000000;
}
.main-header__wrapper {
  max-width: 320px;
  margin: 0 auto;
}
.main-header__navigation {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  padding-top: 22px;
}
.main-header__navigation-mobile {
  display: flex;
}
.main-header__navigation-tablet {
  display: none;
}
.main-header__navigation-desktop {
  display: none;
}
.main-header__logo {
  display: block;
  width: 86px;
  margin: 0 auto;
  margin-bottom: 22px;
}
.main-header__logo:hover {
  opacity: 60%;
}
.main-header__logo:active {
  opacity: 30%;
}
.main-header__toggle {
  width: 20px;
  height: 14px;
  position: absolute;
  right: 30px;
  top: 32px;
  cursor: pointer;
  border: none;
  padding: 0;
  background-color: #ffffff;
}
.main-header__list {
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}
.main-header__list--mobile {
  margin: 0;
  list-style-type: none;
}
.main-header__item--mobile {
  width: 100vw;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.main-header__item--mobile:first-child {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.main-header__item--mobile:last-child {
  border-bottom: none;
}
.main-header__link {
  text-decoration: none;
  color: #000000;
}
.main-header__link:hover {
  opacity: 60%;
}
.main-header__link:active {
  opacity: 30%;
}
.main-header__link--mobile {
  display: inline-block;
  width: 100%;
  padding: 28.5px 46px 26.5px 76px;
}
.main-header__link--mobile-search {
  position: relative;
}
.main-header__link--mobile-search:hover {
  opacity: 100%;
}
.main-header__link--mobile-search:hover .main-header__search-icon {
  opacity: 100%;
  fill: #63d1bb;
}
.main-header__link--mobile-search:hover span {
  opacity: 60%;
}
.main-header__link--mobile-search:active .main-header__search-icon {
  opacity: 30%;
}
.main-header__link--mobile-search:active span {
  opacity: 30%;
}
.main-header__search-icon {
  opacity: 0.2;
  position: absolute;
  width: 18px;
  height: 18px;
  left: 30px;
  top: 32px;
}
.main-header__link--mobile-cart {
  position: relative;
}
.main-header__link--mobile-cart:hover {
  opacity: 100%;
}
.main-header__link--mobile-cart:hover .main-header__cart-icon {
  opacity: 100%;
  fill: #63d1bb;
}
.main-header__link--mobile-cart:hover span {
  opacity: 60%;
}
.main-header__link--mobile-cart:active .main-header__cart-icon {
  opacity: 30%;
}
.main-header__link--mobile-cart:active span {
  opacity: 30%;
}
.main-header__cart-icon {
  opacity: 0.2;
  position: absolute;
  width: 18px;
  height: 18px;
  left: 30px;
  top: 32px;
}
.main-header__navigation-mobile--opened .main-header__list-container {
  top: 79px;
  left: 0;
  width: 100vw;
  position: absolute;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.main-header__navigation-mobile--opened .main-header__toggle::before {
  content: "";
  position: absolute;
  width: 21px;
  height: 20px;
  right: 0;
  bottom: 0;
  background-image: url("../img/menu-close-icon.svg");
}
.main-header__navigation-mobile--closed .main-header__list-container {
  display: none;
}
.main-header__navigation-mobile--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 14px;
  right: 0;
  top: 0;
  background-image: url("../img/menu-open-icon.svg");
}
.main-header__navigation-mobile--nojs .main-header__toggle {
  display: none;
}
.main-header__navigation-mobile--nojs {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}
.main-header__navigation-mobile--nojs .main-header__list-container {
  position: static;
  margin: 0 auto;
}
.main-header__navigation-mobile--nojs .main-header__item--mobile:first-child {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.main-header__navigation-mobile--nojs .main-header__item--mobile:last-child {
  border-bottom: none;
}
@media (min-width: 768px) {
  .main-header__navigation {
    padding-top: 0;
    padding-bottom: 10px;
  }
  .main-header__navigation-mobile {
    display: none;
  }
  .main-header__navigation-tablet {
    display: flex;
    flex-wrap: wrap;
  }
  .main-header {
    padding: 0;
  }
  .main-header__wrapper {
    position: relative;
    max-width: 668px;
    margin: 0 auto;
  }
  .main-header__list--tablet-main,
  .main-header__list--tablet-secondary {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .main-header__list--tablet-main {
    margin-left: auto;
  }
  .main-header__list--tablet-secondary {
    max-width: 360px;
    position: absolute;
    top: 90px;
    z-index: 1;
  }
  .main-header__item--tablet {
    padding: 0;
    font-size: 15px;
    padding-right: 30px;
  }
  .main-header__link--tablet-search {
    width: 18px;
    height: 18px;
    padding: 36px 31px 34px 33px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
  .main-header__item--tablet-search {
    padding-top: 18px;
  }
  .main-header__link--tablet-cart {
    padding: 32.5px 0 30px 73px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
  .main-header__item--tablet-cart {
    font-size: 17px;
    padding: 0;
    padding-top: 20px;
  }
  .main-header__logo--tablet {
    padding-top: 12px;
    margin: 0;
  }
}
@media (min-width: 1150px) {
  .main-header {
    padding: 0;
  }
  .main-header__wrapper {
    position: relative;
    max-width: 1050px;
    margin: 0 auto;
  }
  .main-header__navigation {
    padding-top: 0;
    padding-bottom: 0;
  }
  .main-header__navigation-tablet {
    display: none;
  }
  .main-header__navigation-desktop {
    display: flex;
    justify-content: space-between;
  }
  .main-header__list--desktop-main,
  .main-header__list--desktop-secondary {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .main-header__item--desktop {
    padding: 0;
    padding-top: 20px;
    font-size: 17px;
  }
  .main-header__item--desktop-catalog {
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    padding-top: 30px;
  }
  .main-header__link--desktop-catalog {
    padding: 32.5px 30px 33.5px 0;
  }
  .main-header__link--desktop-order {
    padding: 32.5px 0 33.5px 30px;
  }
  .main-header__item--desktop-order {
    padding-top: 30px;
  }
  .main-header__link--desktop-search {
    width: 18px;
    height: 18px;
    padding: 36px 31px 34px 33px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
  .main-header__item--desktop-search {
    padding-top: 30px;
  }
  .main-header__link--desktop-cart {
    padding: 32.5px 0 34px 73px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
  .main-header__item--desktop-cart {
    font-size: 17px;
    padding: 0;
    padding-top: 30px;
    padding-right: 0;
  }
  .main-header__logo--desktop {
    margin: 0;
    margin-right: 150px;
    padding-top: 10px;
  }
}
.main-container {
  max-width: 260px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-container {
    max-width: 668px;
    padding: 0 50px;
  }
  .main-container--form {
    min-width: 768px;
    padding: 0;
    margin: 0;
  }
}
@media (min-width: 1150px) {
  .main-container {
    max-width: 1050px;
    padding: 0 50px;
  }
  .main-container--form {
    min-width: 1150px;
    padding: 0;
    margin: 0;
  }
}
