.featured-card {
  margin-bottom: 73px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.featured-card::after {
  content: "";
  position: absolute;
  width: 768px;
  height: 6px;
  background-image: url("../img/zigzag-mobile.svg");
  bottom: -36px;
  left: -250px;
}
.featured-card__title {
